const mutations = {
    setCount: (state, count) => (state.count = count),
    setConditions: (state, conditions) => (state.conditions = conditions),
    // setSubCondition: (state, ) => (state.),
    newCondition: (state, condition) => state.conditions.unshift(condition),
    removeCondition: (state, id) => state.conditions = state.conditions.filter(condition => condition.id !== id),
    updateCondition: (state, updCondition) => {
        const index = state.todos.findIndex(condition => condition.id === updCondition.id);
        if(index !== -1) {
            state.conditions.splice(index, 1, updCondition);
        }
    },
    // updateSubCondition: (state, id, subconditionType, subconditionObject)
};

export default {
    state,
    getters,
    actions,
    mutations
};