<template>
    <div id="tab-respondent">
        <v-app id="inspire">
          <div v-bind:key="condition.id" v-for="condition in allConditions">
              <ConditionItem v-bind:condition="condition" />
          </div>
          <AddCondition />
          <button @click="addEmptyCondition()"> Добавить пустое условие </button>
        </v-app>
    </div>
</template>

<script>
import ConditionItem from '../condition/ConditionItem.vue';
import AddCondition from '../condition/AddCondition.vue';
import { mapGetters, mapActions } from 'vuex';

export default {
  name: "TabRespondent",
  components: {
    ConditionItem,
    AddCondition
  },
  methods: {
    ...mapActions(["addCondition", "updateCount"]),
    ...mapGetters(["getCount"]),
    addEmptyCondition() {
      let i = this.getCount();
      i ++;
      this.updateCount(i);
      const updCondition = {
          id: i,
          title: "Условие " + i,
          completed: false,
          conditionType: 4,
          subconditionObject: "object"
      }

      this.addCondition(updCondition);
    }
  },
  computed: mapGetters(["allConditions"]),

}
</script>

<style>

button {
  background-color: coral;
}

</style>