<template>
    <div class="condition-item" v-bind:class="{'is-complete':condition.completed}">
        <!-- title -->
        
        <h3> {{condition.title}} </h3>
        
        <!-- choose option -->
        <p>
            <label for="cars">{{ condition.id }}:</label>

            <select id="cars" class="rounded" @change="setSelectedIndex($event)">
                <option disabled selected value> Выберите условие </option>
                <option value="subcondition1"> Возраст респондента </option>
                <option value="subcondition2"> Тип карты лояльности </option>
                <option value="subcondition3"> Статус карты лояльности </option>
                <option value="subcondition4"> Подусловие 4 </option>
                <option value="subcondition5"> Подусловие 5 </option>
                <option value="subcondition6"> Подусловие 6 </option>
                <option value="subcondition7"> Подусловие 7 </option>
                <option value="subcondition8"> Подусловие 8 </option>
                <option value="subcondition9"> Подусловие 9 </option>
                <option value="subcondition10"> Подусловие 10 </option>
            </select>
        </p>
        <SubCondition1 v-if="showCondition(1)"/>
        <SubCondition2 v-if="showCondition(2)"/>
        <SubCondition3 v-show="showCondition(3)"/>
        <!-- delete button -->
        <button @click="deleteCondition(condition.id)">x</button>

    </div>
</template>

<script>
import SubCondition1 from './subconditions/SubCondition1';
import SubCondition2 from './subconditions/SubCondition2';
import SubCondition3 from './subconditions/SubCondition3';
import { mapActions } from 'vuex';

// TODO optionselectedindex possibly change property to computed
var optionselectedindex =1;

export default {
  name: "ConditionItem",
  props: ["condition"],
  components: {
    SubCondition1,
    SubCondition2,
    SubCondition3
  },
  methods:  {
      ...mapActions(['deleteCondition']),
    setSelectedIndex(e) {
        // Get selected number
        const option = parseInt(e.target.options.selectedIndex);
        optionselectedindex = option;
        // forceUpdate is very important instruction but not the best solution
        // alternative approach is based on :key
        this.$forceUpdate();

    },

    showCondition(index) {
        if (index == optionselectedindex) return true;
        else return false;
    }

  }     
}
</script>

<style scoped>

.is-complete {
    text-decoration: line-through;
}
</style>
