<template>
  <div>
      <button @click="addNewConditionHello(), $emit('add-new-condition')">Добавить условие</button>

      <form @submit="addCondition">
          <!-- <input type="text" v-model="title" name="title" placeholder="Add Todo..."> -->
          <input type="submit" value="Добавить условие" class="btn">
          <!-- TODO do we really need a individual file for a single button -->
      </form>
  </div>
</template>

<script>
import { mapActions } from 'vuex';

export default {
  name: "AddCondition",
  data() {
      return {
          title: ''
      }
  },
  methods: {
      ...mapActions(['addNewConditionHello']),
      addCondition(e) {
         // create constant new Condition with the same id , after that the id and the title will be changed 
         e.preventDefault(); 
         const newCondition = {
             id: 5,
             title: "this.title",
             completed: false 
         }
         // Send up to parent
         this.$emit('add-condition', newCondition); 

         this.title = '';
      }
  }

}
</script>

